import { motion } from "framer-motion";
import React, { useState, useEffect, useMemo } from "react";
import "./App.css";
import CryptoJS from "crypto-js";
import { useTheme } from "./ThemeContext";
import {
  Button,
  Card,
  CardContent,
  Typography,
  Box,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  CircularProgress,
  Snackbar,
  ThemeProvider,
  createTheme,
  CssBaseline,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import ThemeToggleButton from "./ThemeToggleButton";

function Quiz() {
  const [questions, setQuestions] = useState([]);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, setScore] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [feedback, setFeedback] = useState(null);
  const [selectedSection, setSelectedSection] = useState("");
  const [selectedPart, setSelectedPart] = useState("");
  const [loading, setLoading] = useState(false);
  const [highlightCorrectAnswer, setHighlightCorrectAnswer] = useState(false);
  const [error, setError] = useState(false); // State to track errors
  const [isHovering, setIsHovering] = useState(false); // State to track hover over "Auto next"
  const [intervalNm, setIntervalNm] = useState(null); // State to store interval ID
  const { mode } = useTheme();
  const question = questions[currentQuestion];

  const sections = [
    {
      value: "01",
      label: "Group 1",
      parts: 12,
      auto_skip_to_implement: true,
      auto_skip_pausable: true,
      autoskip_to_work_with_parts: [1, 4],
    },
    {
      value: "02",
      label: "Group 2",
      parts: 5,
      auto_skip_to_implement: false,
      auto_skip_pausable: true,
      autoskip_to_work_with_parts: [1, 4],
    },
    {
      value: "03",
      label: "Group 3",
      parts: 2,
      auto_skip_to_implement: false,
      auto_skip_pausable: true,
      autoskip_to_work_with_parts: [1, 4],
    },
  ];
  // Looks for the section object in the sections array where value matches selectedSection.
  // Accesses the parts property of the matching section, which specifies the number of parts in that section.
  const partsForSelectedSection = selectedSection
    ? Array.from(
        { length: sections.find((s) => s.value === selectedSection).parts },
        (_, index) => ({
          value: (index + 1).toString().padStart(3, "0"),
          label: `Part ${index + 1}`,
        })
      )
    : [];

  // Function to load and decrypt questions based on selected section and part
  const loadQuestions = async (section, part) => {
    setLoading(true);
    try {
      const encryptedData = await import(
        `./encryptedQuestions${section}${part}.json`
      );
      const secretKey = "your-secret-key";
      const decryptedData = CryptoJS.AES.decrypt(
        encryptedData.data,
        secretKey
      ).toString(CryptoJS.enc.Utf8);
      setQuestions(JSON.parse(decryptedData));
      setCurrentQuestion(0);
      setScore(0);
      setShowScore(false);
      setHighlightCorrectAnswer(false);
    } catch (error) {
      console.error("Error loading questions:", error);
      setError(true);
      return (
        <Snackbar
          open={error}
          autoHideDuration={6000}
          onClose={() => setError(false)}
          message="Error loading questions. Please reload."
        />
      );
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    // console.log("UE isHovering", isHovering); // This will log after the state has been updated
  }, [isHovering]); // Dependency array ensures this effect runs whenever isHovering changes

  useEffect(() => {
    if (selectedPart) {
      loadQuestions(selectedSection, selectedPart); // Pass both section and part to loadQuestions
    }
  }, [selectedPart, selectedSection]);

  // Use a functional update to always get the most current state
  const si = () => {
    const intervalId = setInterval(() => {
      console.log("Hovering On:", isHovering);

      // Only trigger handleAnswerClick if questions are loaded and there are more questions
      if (
        !isHovering &&
        questions.length > 0 &&
        currentQuestion < questions.length
      ) {
        const currentQ = questions[currentQuestion];
        const correctAnswer = currentQ.answer; // Get the correct answer dynamically from the current question

        // Log currentQuestion and the correct answer for debugging
        // console.log(
        //   `Question ${currentQuestion + 1}:`,
        //   currentQ.question,
        //   "Correct Answer:",
        //   correctAnswer
        // );

        // Trigger handleAnswerClick with the correct answer dynamically
        handleAnswerClick(correctAnswer);
      }
    },35000); //35000
    setIntervalNm(intervalId);
    return intervalId;
  };

  useEffect(() => {
    // Find the currently selected section
    const currentSection = sections.find(
      (section) => section.value === selectedSection
    );
    // console.log("currentSection?.auto_skip_to_implement",currentSection?.auto_skip_to_implement)
    // Run interval logic only if auto_skip_to_implement is true
    if (currentSection?.auto_skip_to_implement) {
      const intervalNm = si(); // Call si to start the interval

      // Cleanup function to clear the interval
      return () => {
        clearInterval(intervalNm); // Clear the interval when the component unmounts or dependencies change
      };
    }

    // If auto_skip_to_implement is false, ensure no interval is started
    return () => {};
  }, [selectedSection, questions, currentQuestion, isHovering]); // Trigger when these dependencies change


  const handleAnswerClick = (option) => {
    setSelectedAnswer(option);

    if (option === question.answer) {
      setScore(score + 1);
      setFeedback("correct");
      setHighlightCorrectAnswer(false);
    } else {
      setFeedback("incorrect");
      setHighlightCorrectAnswer(true);

      setTimeout(() => {
        setHighlightCorrectAnswer(false);
      }, 5000);
    }

    setTimeout(() => {
      setFeedback(null);
      setSelectedAnswer(null);
      const nextQuestion = currentQuestion + 1;
      if (nextQuestion < questions.length) {
        setCurrentQuestion(nextQuestion);
        setHighlightCorrectAnswer(false);
      } else {
        setShowScore(true);
      }
    }, 1000);
  };

  const handleBack = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
    }
  };

  const handleReturnToPart = () => {
    setSelectedPart("");
    setShowScore(false);
    setScore(0);
  };

  const handleReturnToSection = () => {
    setSelectedSection("");
    setSelectedPart("");
    setShowScore(false);
    setScore(0);
  };

  // Log the updated isHovering state after it changes

  const handleMouseEnter = () => {
    // Use functional form of setState to update based on the previous state
    setIsHovering((prevState) => {
      // console.log("Mouse enter (before update):", prevState); // Logs previous state
      return true;
    });
  };

  const handleMouseLeave = () => {
    // Use functional form of setState to update based on the previous state
    setIsHovering((prevState) => {
      // console.log("Mouse leave (before update):", prevState); // Logs previous state
      return false;
    });
  };

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode, // This now dynamically switches between 'light' and 'dark'
          primary: {
            main: "#1976d2",
          },
          secondary: {
            main: "#9C27B0",
          },
        },
      }),
    [mode]
  );

  if (error)
    return <div>Error loading questions. Please reload page again.</div>;

  if (loading) {
    return <CircularProgress />;
  }

  if (!selectedSection) {
    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 2.5 }}
        >
          <Box sx={{ position: "absolute", top: 16, right: 16, zIndex: 1300 }}>
            <ThemeToggleButton />
          </Box>
          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            minHeight="100vh"
            // bgcolor="#f5f5f5"
          >
            <Card sx={{ width: 500, p: 3 }}>
              <CardContent>
                <Typography variant="h5" gutterBottom>
                  Select a Group
                </Typography>
                <FormControl fullWidth>
                  <InputLabel id="select-section-label">
                    Select Group
                  </InputLabel>
                  <Select
                    labelId="select-section-label"
                    value={selectedSection}
                    onChange={(e) => setSelectedSection(e.target.value)}
                    label="Select Section"
                  >
                    {sections.map((section) => (
                      <MenuItem key={section.value} value={section.value}>
                        {section.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </CardContent>
            </Card>
          </Box>
        </motion.div>
      </ThemeProvider>
    );
  }

  if (!selectedPart) {
    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 2.5 }}
        >
          <Box sx={{ position: "absolute", top: 16, right: 16, zIndex: 1300 }}>
            <ThemeToggleButton />
          </Box>
          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            minHeight="100vh"
            // bgcolor="#f5f5f5"
          >
            <Card sx={{ width: 500, p: 3, backgroundColor: "lightblue" }}>
              <CardContent>
                <Typography variant="h5" gutterBottom>
                  Select a Part from Group {selectedSection}
                </Typography>
                <FormControl fullWidth>
                  <InputLabel id="select-part-label">Select Part</InputLabel>
                  <Select
                    labelId="select-part-label"
                    value={selectedPart}
                    onChange={(e) => setSelectedPart(e.target.value)}
                    label="Select Part"
                  >
                    {partsForSelectedSection.map((part) => (
                      <MenuItem key={part.value} value={part.value}>
                        {part.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </CardContent>
            </Card>
          </Box>
        </motion.div>
      </ThemeProvider>
    );
  }

  if (questions.length === 0) {
    return <CircularProgress />;
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 2.5 }}
      >
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          minHeight="100vh"
          // bgcolor="#f5f5f5"
        >
          <Card sx={{ width: 500, p: 3, overflowY: "auto" }}>
            <CardContent
              style={{ whiteSpace: "pre-wrap", wordBreak: "break-word" }}
            >
              {showScore ? (
                <>
                  <Typography
                    variant="h5"
                    align="center"
                    style={{ whiteSpace: "pre-wrap", wordBreak: "break-word" }}
                  >
                    Score: {score} out of {questions.length}{" "}
                    <span style={{ fontSize: ".77rem" }}>
                      ({selectedSection}/{selectedPart})
                    </span>
                  </Typography>
                  <Box display="flex" justifyContent="space-between" mt={2}>
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={handleReturnToPart}
                      sx={{ textTransform: "none" }}
                    >
                      Return to Select a Part
                    </Button>
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={handleReturnToSection}
                      sx={{ textTransform: "none" }}
                    >
                      Return to Selection of Group
                    </Button>
                  </Box>
                </>
              ) : (
                <>
                  <Typography variant="h6" gutterBottom>
                    Question {currentQuestion + 1}/
                    <span style={{ fontSize: ".97rem" }}>
                      {questions.length}
                    </span>{" "}
                    <span style={{ fontSize: ".77rem" }}>
                      (<span style={{ fontSize: ".5rem" }}>g</span>
                      {selectedSection}/
                      <span style={{ fontSize: ".5rem" }}>p</span>
                      {selectedPart})
                      <>
                        {sections.map(
                          (section) =>
                            section.value === selectedSection &&
                            section.auto_skip_pausable &&
                            section.auto_skip_to_implement && (
                              <span
                                key={section.value}
                                onMouseEnter={() => handleMouseEnter()}
                                onMouseLeave={() => handleMouseLeave()}
                                className="auto-n_seconds"
                              >
                                -Auto Next
                              </span>
                            )
                        )}
                      </>
                    </span>
                  </Typography>
                  <Typography variant="h5" gutterBottom>
                    {question.question}
                    {question.code ? (
                      question.code.split("\n").map((line, index) => (
                        <React.Fragment key={index}>
                          {line}
                          <br />
                        </React.Fragment>
                      ))
                    ) : (
                      <span></span> // Or leave this part empty if you prefer
                    )}
                  </Typography>

                  <Box mt={2}>
                    {Object.entries(question.options).map(([key, value]) => (
                      <Button
                        key={key}
                        variant="contained"
                        color={
                          selectedAnswer === key
                            ? feedback === "correct"
                              ? "success"
                              : "error"
                            : "primary"
                        }
                        onClick={() => handleAnswerClick(key)}
                        sx={{
                          display: "block",
                          width: "100%",
                          my: 1,
                          textTransform: "none",
                          backgroundColor:
                            highlightCorrectAnswer && key === question.answer
                              ? "green"
                              : undefined,
                        }}
                      >
                        {value}
                      </Button>
                    ))}
                  </Box>

                  <Box
                    mt={2}
                    display="flex"
                    justifyContent="space-between"
                    alignItems="center"
                  >
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={handleBack}
                      sx={{
                        minWidth: 120, // Ensures the button doesn't shrink
                        flexShrink: 0, // Prevents shrinking
                        width: "auto", // Keeps it flexible
                        padding: "10px 20px",
                      }}
                    >
                      Back
                    </Button>

                    {feedback && (
                      <Box
                        mt={0} // Adjusted for alignment
                        display="inline-flex" // Keeps the icon inline without affecting layout
                        alignItems="center"
                        justifyContent="center"
                        sx={{
                          marginLeft: "10px", // Controls space between icon and buttons
                          marginRight: "10px", // Controls space between icon and buttons
                        }}
                      >
                        {feedback === "correct" ? (
                          <CheckCircleIcon color="success" fontSize="large" />
                        ) : (
                          <HighlightOffIcon color="error" fontSize="large" />
                        )}
                      </Box>
                    )}

                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => handleAnswerClick(selectedAnswer)}
                      sx={{
                        minWidth: 120, // Ensure minimum width
                        flexShrink: 0, // Prevents shrinking
                        width: "auto", // Allows the button to resize as needed
                        padding: "10px 20px",
                        transition: "none", // Prevents transition resizing
                      }}
                    >
                      Next Question
                    </Button>
                  </Box>
                </>
              )}
            </CardContent>
          </Card>
        </Box>
        <Box sx={{ position: "absolute", top: 16, right: 16, zIndex: 1300 }}>
          <ThemeToggleButton />
        </Box>
      </motion.div>
    </ThemeProvider>
  );
}

export default Quiz;
